*{margin:0;padding:0;box-sizing:border-box}body{text-align:center;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html,body,#root{height:100%}#root{display:flex;flex-direction:column}

.button{display:inline-block;background-color:var(--primaryColor);color:var(--mainWhite);font:inherit;text-decoration:none;border:1px solid var(--primaryColor);border-radius:4px;margin:0.5rem;padding:0.2rem;transition:var(--mainTransition);cursor:pointer}.button.button-icon{background:transparent;border:none;color:var(--primaryColor);font-size:1.5rem;margin:0;padding:0}.button.inverted{background:transparent;color:var(--primaryColor)}

.Header{flex-shrink:0;position:-webkit-sticky;position:sticky;top:0;display:flex;justify-content:space-between;align-items:center;padding:0 1.5rem;height:var(--header-height);background:var(--mainGrey);border-bottom:3px solid var(--primaryColor);z-index:1}.Header .logo{font-size:1.75rem;text-decoration:none;font-weight:bold}.Header .logo :first-child{color:var(--mainBlack)}.Header .logo :last-child{color:var(--primaryColor)}.Header .cart-button{position:relative}.Header .cart-button .cart-counter{position:absolute;top:0;right:-0.5rem;text-align:center;width:1rem;height:1rem;background-color:var(--mainWhite);border:1px solid var(--mainBlack);border-radius:50%;color:var(--mainBlack);font-size:0.65rem;overflow:hidden}

.Loader{height:100%;width:100%;display:flex;justify-content:center;align-items:center}

.Content{flex:1 1}

.Menu{position:fixed;top:var(--header-height);left:0;height:100%;width:100%;border-right:4px solid var(--primaryColor);background-color:var(--mainGrey);z-index:1;transition:var(--mainTransition);transform:translateX(0)}@media (min-width: 576px){.Menu{width:20rem}}.Menu ul{list-style:none}.Menu ul li a{display:block;text-decoration:none;color:var(--mainBlack);font-size:1.5rem;text-transform:capitalize;text-align:start;padding:0.5rem 1.5rem;background:transparent;transition:var(--mainTransition)}.Menu ul li a:hover{background:var(--primaryColor);color:var(--mainWhite);padding:0.5rem 1.5rem 0.5rem 2.5rem}.Menu ul li a.current-page{background:var(--primaryColor);color:var(--mainWhite);padding:0.5rem 1.5rem 0.5rem 2.5rem}.Menu.hidden{transform:translateX(-100%)}

.CartItem{display:flex;width:100%;justify-content:space-between;margin:0.5rem 0}.CartItem .cart-item__img{flex:0 0 80px}.CartItem .cart-item__img img{width:100%}.CartItem .cart-item__title-amount-wrapper{flex:1 1 auto;display:flex;flex-flow:row wrap;justify-content:space-around;align-items:center;align-content:space-between;padding:0.5rem 0}.CartItem .cart-item__price-remove-wrapper{flex:0 1 15%;display:flex;flex-flow:row wrap;justify-content:center;align-items:center;align-content:space-between;min-width:3rem;max-width:6rem;padding:0.5rem 0}.CartItem .cart-item__title{text-transform:uppercase;flex:0 1 18rem}.CartItem .cart-item__amount{grid-area:amount;display:flex;align-content:center}.CartItem .cart-item__amount input{width:2rem;text-align:center;margin:0.3rem}.CartItem .cart-item__amount input::-webkit-outer-spin-button,.CartItem .cart-item__amount input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}.CartItem .cart-item__amount input[type="number"]{-moz-appearance:textfield}.CartItem .cart-item__price{flex:0 0 3rem}.CartItem .cart-item__price-apiece{font-size:0.75rem;color:var(--darkGrey)}.CartItem .cart-item__price-apiece::before{content:"("}.CartItem .cart-item__price-apiece::after{content:")"}.CartItem .cart-item__remove{flex:0 0 3rem;align-self:start}.CartItem .hidden{visibility:hidden}

.CartList ul{list-style:none}.CartList .cart__total{margin:0.5rem auto}.CartList .cart__total .cart__total-sum:before{content:"$"}.CartList .cart__button{display:inline-block;background-color:var(--primaryColor);color:var(--mainWhite);text-decoration:none;border:1px solid var(--primaryColor);border-radius:4px;padding:0.2rem;margin:0.5rem;cursor:pointer}

.Cart{position:fixed;top:var(--header-height);right:0;height:100%;width:100%;border-left:4px solid var(--primaryColor);background-color:var(--mainGrey);z-index:1;transition:var(--mainTransition);transform:translateX(0);overflow:auto;padding:0.2rem}@media (min-width: 576px){.Cart{width:20rem}}.Cart.hidden{transform:translateX(100%)}.Cart .cart__footer{display:flex;justify-content:center}

.Footer{flex-shrink:0;display:flex;justify-content:space-around;align-items:center;flex-wrap:wrap;background:var(--darkGrey);color:var(--mainWhite);padding:0.5rem}.Footer .social-media{display:flex;justify-content:space-between;align-items:center;margin:0.2rem}.Footer .social-media a{color:var(--mainWhite);font-size:1.5rem;margin:0 1.5rem;transition:var(--mainTransition)}.Footer .social-media a:hover{color:var(--primaryColor)}

:root{--header-height: 2.5rem;--primaryColor: #5fb7ea;--primaryRGBA: rgba(0, 0, 0, 0.3);--mainGrey: #fafafa;--darkGrey: #5f5e5e;--mainWhite: #fff;--mainBlack: #222;--mainTransition: all 0.3s ease-in-out;--mainSpacing: 0.3rem}

